import khSelect from '@/components/product/kh/khSelect.vue'
/*x*/
import tlSelect from '@/components/product/tl/tlSelect.vue'
//
import tlEdit from '@/components/product/tl/tlEdit.vue'
//
import hbSelect from '@/components/product/hb/hbSelect.vue'

//
import material from '@/components/user/material.vue'

//vuex
import testStore from '@/components/store'
//
Vue.use(vueRouter)


let router =  new vueRouter({	
//	mode:'history',
	routes: [
		
	    { //
	      path: '/',
	      name: 'Index',
	      component: Index
	    },
	    { 
	 		path: '/bbsImgs', component: editImgs,  //
	 		meta:{
	 			title:""
	 		}
	 	},
	 	{ 
	 		path: '/huaceImgs', component: huaceImgs,  //
	 		meta:{
	 			title:""
	 		}
	 	},
//		{
//			path:'/', component:Home, // home
//			children: [
//	            { path: '/orderList', component: orderList }
////	            { path: '/page5', component: Page5, name: '5' }
//     		 ]
//		},
		
		{
			path:'/User', component:User, //
			meta:{
	 			title:""
	 		}
		},
		{
			path:'/NewAddress', component:NewAddress, //
			meta:{
	 			title:""
	 		}
		},
		{
			path:'/Address', component:Address //
		},
		{
			path:'/OrderList', component:OrderList //
		},
		{
			path:'/OrderStatus', component:OrderStatus //
		},
		{
			path:'/cart', component:Cart //
		},
		{
			path:'/payOrder', component:payOrder //
		},
		{
			path:'/bbsSelect', component:bbsSelect //
		},
		{
			path:'/bbsMsg', component:bbsMsg //
		},
		{
			path:'/hjMsg', component:hjMsg //
		},
		{
			path:'/editImg', component:editImg //
		},
		{
			path:'/workList', component:workList //
		},
		{
			path:'/khSelect', component:khSelect //			
		},
		{
			path:'/huaceMsg', component:huaceMsg //
		},
		{
			path:'/hbMsg', component:hbMsg //
		},
		{
			path:'/khMsg', component:khMsg //
		},
		{
			path:'/tlMsg', component:tlMsg //
		},
		{
			path:'/cthMsg', component:cthMsg //
			
		},
		{
			path:'/huaceSelect', component:huaceSelect, //
			meta:{
	 			title:""
	 		}
		},
		{
			path:'/tlSelect', component:tlSelect //
			,meta:{
	 			title:""
	 		}
		},
		{
			path:'/tlEdit', component:tlEdit //
			
		},
		{
			path:'/hbSelect', component:hbSelect //
			
		},
		{
			path:'/material', component:material //
			
		},
	    {   
	     	path:'*',
	     	//404
//	    		component: noFound
	    		// redirect ,
	    		redirect:'/bbsEdit'	    		
	    }
	    
	  ]
})
 
router.beforeEach((to, from, next) => {  
	next();
	(function(i, s, o, g, r, a, m) {
	i['GoogleAnalyticsObject'] = r;
	i[r] = i[r] || function() {
		(i[r].q = i[r].q || []).push(arguments)
	}, i[r].l = 1 * new Date();
	a = s.createElement(o),
		m = s.getElementsByTagName(o)[0];
	a.async = 1;
	a.src = g;
	m.parentNode.insertBefore(a, m)
})(window, document, 'script', 'https://www.google-analytics.com/analytics.js', 'ga');

  ga('create', 'UA-105311960-1', 'auto');
  ga('send', 'pageview');
}); 

//
export default  router




