//directive js
import Vue from 'vue'
//  v-DomHeight indexswipeDOM
var DomHeight = Vue.directive('DomHeight', {
  //  DOM 
  inserted: function (el) { //dom
    var oH = $(window).height()- $(el).height();
    $("#index .swipeDiv").css("height",oH+'px');
  }
})

export {DomHeight}



