import Vue from "vue"
import Vuex from "vuex"
import axios from "axios"

Vue.use(Vuex)

import Api from '../api.js'

//
let store = new Vuex.Store({
	state:{
		count:100,
		bbs:{
			materialModel:false, //
			material:[], //
			footerData:[]//
		}
	},
	getters:{ //
		filterCount(state){
			return state.count >=120 ? 120:state.count
		},
		footerDataGet(state){
//			return state.bbs.footerData
		}
	},
	mutations:{ //--> actions
		getMaterials(state,obj){ // obj
			//
			obj.results.forEach(val=>{
				val.isTrue = false 
			})
			state.bbs.material = obj.results;
			$(".el-loading-mask").remove();
		},
		slectFile(state){//
//			var arrImg = [];
			state.bbs.material.forEach(val=>{
				if(val.isTrue){
					state.bbs.footerData.unshift(val)
				}
			})
			//
//			state.bbs.footerData.push(arrImg);
//			console.log(state.bbs.footerData)
		}
	},
	actions:{ //actionsvuex ,mutations commitmutations
		//es6 
//		addAction({commit,dispatch}){
//			setTimeout(()=>{//
//				//
//				commit("addIncrement",{n:6})
//				//,
//				dispatch("textAction",{text:''})
//			},2000)
//		},
//		textAction(context,obj){
//			console.log('test')			
//			console.log('...'+obj.text)			
//		},
		getMaterial({commit,dispatch}){//
				var paramJson ={
					ignore:"true",
		            format:"json",
		            userDbId:"2221214",
		            status:1,
		            pageNum:0,
		            pageSize:50,
		            sort:"uploadDt",
		            order:"desc",
		            category: ""
		        } 
		        //
		        Api.Material.MaterialData(paramJson).then((res)=>{
		        		console.log('')
		        		commit("getMaterials",res.data)
		        })
		}
		
	}
})
// stroe
export default store



