Vue.filter("sum", function(value) {   // Vue.filter() ,Vue
    return value + 40;
});
Vue.filter("json", function(value) {   // Vue.filter() ,Vue
   console.log(value)
   return JSON.stringify(value);
});
Vue.filter("splitSku",function(value){
	if (value ) {
		var arr = value.split('.');
//		if(arr.length > 2){
//			var newArr = [];
//			for(var i=0; i<arr.length-2; i++){
//				newArr.push(arr[i])
//			}
//			return newArr.join('.');
//		}else{
//			return arr[0];
//		}
		return arr[0];
		
	}
});
Vue.filter("splitSkuLast",function(value){
	if (value) {
		var arr = value.split('.');
		return arr.slice(1,arr.length).join('.')
	}
})

Vue.filter("splitAddress",function(value){
	if (value) {
		
		return value.length > 10 ? value.substr(0,10).concat('...'):value;
	}
})

Vue.filter("toFixedTwo",function(value){
	if (value) {
		
		return parseFloat(value.toFixed(2));
	}
})
Vue.filter('subStr',function(value){

	if(value){
		return value.length > 9 ? value.substr(0,9).concat('...'):value; 
	}
	
})



